import {createSlice } from "@reduxjs/toolkit";
const initialState = {
	cargando: false,
}

const cargandoSlice = createSlice ({
	name: "cargandoSlice",
	initialState,
	reducers: {
		setCargando: (state, action) => {
            state.cargando = action.payload;
        },
	}

})

export const { setCargando } = cargandoSlice.actions;
export default cargandoSlice.reducer;