import React from 'react'
import { Toast, ToastContainer } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { setAlerta } from '../redux/features/alertaSlice'

const Alerta = () => {
    const alerta = useSelector((state) => state.alertaSlice.alerta);
    const dispatch = useDispatch()

    return (
        <ToastContainer className="position-fixed top-0 end-0 p-3"
            style={{ zIndex: 1070 }}>
            <Toast onClose={() => dispatch(setAlerta({ ...alerta, mostrar: false }))} show={alerta.mostrar} bg={alerta.tipo} delay={3000} autohide>
                <Toast.Header>
                    <strong className="me-auto">{alerta.titulo}</strong>
                </Toast.Header>
                <Toast.Body>{alerta.mensaje}</Toast.Body>
            </Toast>
        </ToastContainer>
    );
};

export default Alerta