import React from 'react'
import moment from 'moment';
import { Table } from 'react-bootstrap'
import Evento from './Evento'
import { useSelector } from 'react-redux'

const EventosAnteriores = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos)
    const listaEventos = eventos?.filter(e => moment(e.fecha).isBefore(moment(), 'day'))
        .sort((a, b) => new Date(b.fecha) - new Date(a.fecha));

    return (
        <Table>
            <tbody>
                {listaEventos?.map(e => <Evento key={e.id} {...e} />)}
            </tbody>
        </Table>
    )
}

export default EventosAnteriores