import React, { useEffect, useState } from 'react'
import Chart from 'react-apexcharts';
import { useSelector } from 'react-redux';

const GraficaCantidad = () => {
    const categorias = useSelector((state) => state.eventosSlice.categorias);
    const eventos = useSelector((state) => state.eventosSlice.eventos);
    const [graficoData, setGraficoData] = useState({
        series: [],
        options: {
            chart: {
                type: 'donut',
                height: 350
            },
            labels: [],
            responsive: [{
                breakpoint: 480,
                options: {
                    chart: {
                        width: 200
                    },
                    legend: {
                        position: 'bottom'
                    }
                }
            }]
        }
    });

    useEffect(() => {
        const etiquetas = categorias.map(c => c.tipo);
        const cantidades = categorias.map(c => eventos.filter(e => e.idCategoria === c.id).length);

        setGraficoData(dataAnterior => ({
            ...dataAnterior,
            series: cantidades,
            options: {
                ...dataAnterior.options,
                labels: etiquetas
            }
        }));
    }, [categorias, eventos]);

    return (
        <Chart
            options={graficoData.options}
            series={graficoData.series}
            type="donut"
            height={250}
        />
    )
}

export default GraficaCantidad