import React from 'react'
import { Button } from 'react-bootstrap';
import { CalendarHeartFill, PersonFill } from 'react-bootstrap-icons';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { agregarIniciales } from '../redux/features/eventosSlice';

const NavbarMenu = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleClick = (e) => {
    let localStorage = window.localStorage;
    localStorage.clear();
    dispatch(agregarIniciales([]))
    navigate('/login');
  }

  return (
    <Navbar expand="lg" bg="amarillo" sticky="top">
      <Container className="px-0">
        <Navbar.Brand className="navbar-brand">
          <CalendarHeartFill size={35} className="navbar-brand-icon" />
          BabyTracker
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <div className="ms-auto d-flex">
            <p className="m-auto"><PersonFill size={25}></PersonFill> Bienvenido, <strong>{localStorage.getItem("usuarioLogueado")}</strong></p>
            <Button variant="rosaO" onClick={handleClick} className="ms-2" >Logout</Button>
          </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default NavbarMenu