import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    alerta: {
        mostrar: false,
        titulo: "", 
        mensaje: "", 
        tipo: ""
    },
};

const alertaSlice = createSlice({
    name: "alertaSlice",
    initialState,
    reducers: {
        setAlerta: (state, action) => {
            state.alerta = action.payload;
        },
    }
});

export const { setAlerta } = alertaSlice.actions;
export default alertaSlice.reducer;
