import React from 'react'
import { Card, Container } from 'react-bootstrap'
import LoginForm from './LoginForm'
import { Link } from 'react-router-dom';
import SpinnerCargando from './SpinnerCargando';
import { useSelector } from 'react-redux';

const Login = () => {
    const cargando = useSelector((state) => state.cargandoSlice.cargando);
    return (
        <>
            <SpinnerCargando cargando={cargando}></SpinnerCargando>
            <Container fluid className={`content ${cargando ? 'disabled' : ''}`}>
                <h1 className="text-center my-5">Bienvenido a Babytracker</h1>
                <Card style={{ width: '25%' }} className="m-auto p-3">
                    <LoginForm></LoginForm>
                    <p className="mt-2 mb-0"><span>¿Aún no tienes cuenta?</span> <Link to="/registro">Registrarse</Link></p>
                </Card>
            </Container>
        </>
    )
}

export default Login