import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';
import { registroUsuarioAPI } from '../services';
import { useSelector } from 'react-redux';
import { Col, Row } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { setCargando } from '../redux/features/cargandoSlice';
import { setAlerta } from '../redux/features/alertaSlice'

const RegistroFrom = () => {
    const departamentos = useSelector((state) => state.eventosSlice.departamentos)
    const [valoresActuales, setValores] = useState({});
    const [ciudades, setCiudades] = useState([]);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const handleChange = (e) => {
        const { name, value } = e.target;

        setValores((valoresActuales) => {
            const nuevosValores = { ...valoresActuales };

            if (value === "") {
                delete nuevosValores[name];
            } else {
                nuevosValores[name] = value;
            }
            return nuevosValores;
        });
    };

    const handleChangeDepartamento = async (e) => {
        setValores((valoresActuales) => {
            return {
                ...valoresActuales,
                [e.target.name]:
                    e.target.value,
            };
        });

        fetch(`https://babytracker.develotion.com/ciudades.php?idDepartamento=${e.target.value}`)
            .then(response => response.json())
            .then(json => setCiudades(json.ciudades))
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (!valoresActuales.idDepartamento) {
                throw new Error("Departamento no puede estar vacío");
            }
            if (!valoresActuales.idCiudad) {
                throw new Error("Ciudad no puede estar vacía");
            }
            dispatch(setCargando(true));
            const resultado = await registroUsuarioAPI(valoresActuales);
            dispatch(setAlerta({ mostrar: true, tipo: "verde", titulo: "Éxito", mensaje: "Usuario registrado con éxito" }))
            let localStorage = window.localStorage;
            localStorage.setItem("apikey", resultado.apiKey);
            localStorage.setItem("idusuario", resultado.id);
            localStorage.setItem("usuarioLogueado", valoresActuales.usuario);
            setValores({});
            navigate('/')
        } catch (error) {
            dispatch(setCargando(false))
            dispatch(setAlerta({ mostrar: true, tipo: "rosa", titulo: "Error", mensaje: error.message }))
        }
    }

    return (
        <Form onSubmit={handleSubmit} className="d-flex flex-column">
            <Form.Group className="mb-3" controlId="LoginUsuario">
                <Form.Label>Usuario</Form.Label>
                <Form.Control type="text" placeholder="Ingrese usuario" name="usuario" onChange={handleChange} value={valoresActuales.usuario ?? ""} required />
            </Form.Group>
            <Form.Group className="mb-3" controlId="LoginPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control type="password" placeholder="Password" name="password" onChange={handleChange} value={valoresActuales.password ?? ""} required />
            </Form.Group>
            <Row>
                <Col>
                    <Form.Group>
                        <Form.Label>Departamento</Form.Label>
                        <Form.Select aria-label="Default select example" name="idDepartamento" onChange={handleChangeDepartamento} value={valoresActuales.idDepartamento ?? 0}>
                            <option key={0} value={0} disabled>Seleccione un departamento</option>
                            {departamentos ? departamentos.map(c => <option key={c.id} value={c.id}>{c.nombre}</option>) : <></>}
                        </Form.Select>
                    </Form.Group>
                </Col>
                <Col>
                    {valoresActuales.idDepartamento ?
                        <Form.Group>
                            <Form.Label>Ciudad</Form.Label>
                            <Form.Select aria-label="Default select example" name="idCiudad" onChange={handleChange} value={valoresActuales.idCiudad ?? 0}>
                                <option key={0} value={0} disabled>Seleccione una ciudad</option>
                                {ciudades ? ciudades.map(c => <option key={c.id} value={c.id}>{c.nombre}</option>) : <></>}
                            </Form.Select>
                        </Form.Group> : <p></p>
                    }
                </Col>
            </Row>
            {(Object.keys(valoresActuales).length < 4) ?
                <Button variant="secundary" type="submit" disabled className="mt-2">
                    Registrarse
                </Button> :
                <Button variant="primary" type="submit" className="mt-2">
                    Registrarse
                </Button>}
        </Form>
    )
}

export default RegistroFrom