import './App.css';
import Dashboard from './components/Dashboard';
import Login from './components/Login';
import Registro from './components/Registro';
import { BrowserRouter, Routes, Route} from "react-router-dom" ;
import Alerta from './components/Alerta';
import { useSelector } from 'react-redux';


function App() {
  const alerta = useSelector((state) => state.alertaSlice.alerta);

  return (
    <>
    <BrowserRouter>
      <Routes>
				 <Route path="/login" element={<Login />}></Route>
         <Route path="/registro" element={<Registro />}></Route>
				 <Route path="/" element={<Dashboard />}></Route>
				 <Route path="*" element={<p><strong>Error 404</strong>: Path not found</p>}></Route>
			 </Routes>
    </BrowserRouter>
       <Alerta mostrar={alerta.mostrar} titulo={alerta.titulo} mensaje={alerta.mensaje} tipo={alerta.tipo}></Alerta>
    </>

  );
}

export default App;
