import React from 'react'
import moment from 'moment';
import { Badge, Card, CardBody } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import TimerBiberones from './TimerBiberones';

const Biberones = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos)
    const cantidad = (eventos?.filter(e => e.idCategoria === 35 && e.fecha >= moment(new Date()).format('YYYY-MM-DD')))?.length;
    return (
        <Card className="px-4" bg="lila" border="lila">
            <CardBody className="m-auto ">
                <Card.Title className="text-center">Biberones</Card.Title>
                <h2 className="text-center"><Badge bg="lilaO">{cantidad}</Badge></h2>
                <TimerBiberones />
            </CardBody>
        </Card>
    )
}

export default Biberones