import { configureStore } from '@reduxjs/toolkit'
import eventosSlice from './features/eventosSlice';
import cargandoSlice from './features/cargandoSlice';
import alertaSlice from './features/alertaSlice';


export const store = configureStore({
    reducer: {
        eventosSlice,
        cargandoSlice,
        alertaSlice,
    },
});