import {createSlice } from "@reduxjs/toolkit";
const initialState = {
	eventos: [],
    categorias: [],
    departamentos: [],
}

const eventosSilice = createSlice ({
	name: "eventosSlice",
	initialState,
	reducers: {
		agregarIniciales: (state, action) => {
            state.eventos = action.payload;
        },

        cargarCategorias: (state, action) => {
            state.categorias = action.payload
        },

        cargarDepartamentos: (state, action) => {
            state.departamentos = action.payload
        },

        agregarEvento: (state, action) => {
            state.eventos = [...state.eventos, action.payload];
        },

        eliminarEvento: (state, action) => {
            state.eventos = state.eventos.filter(e => e.id !== action.payload);
        }
	}

})

export const { agregarIniciales, cargarCategorias, cargarDepartamentos, agregarEvento, eliminarEvento } = eventosSilice.actions;
export default eventosSilice.reducer;