import moment from 'moment';
import React, { useState } from 'react'
import { FormControl, FormGroup } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useDispatch, useSelector } from 'react-redux'
import { agregarEventoAPI } from '../services';
import { agregarEvento } from '../redux/features/eventosSlice';
import { setAlerta } from '../redux/features/alertaSlice'

const Agregar = () => {
    const categorias = useSelector((state) => state.eventosSlice.categorias)
    const [valoresActuales, setValores] = useState({ fecha: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'), detalle: "", idUsuario: window.localStorage.getItem("idusuario") });
    const dispatch = useDispatch();

    const handleChange = (e) => {
        setValores((valoresActuales) => {
            return {
                ...valoresActuales,
                [e.target.name]:
                    e.target.type === "datetime-local" ? moment(e.target.value).format('YYYY-MM-DD HH:mm:ss') : e.target.value,
            };
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (!valoresActuales.idCategoria) {
                throw new Error("Categoría no puede estar vacía");
            }
            const resultado = await agregarEventoAPI(valoresActuales);
            dispatch(agregarEvento({ ...valoresActuales, id: resultado.idEvento, idCategoria: parseInt(valoresActuales.idCategoria), idUsuario: parseInt(valoresActuales.idUsuario) }))
            dispatch(setAlerta({ mostrar: true, tipo: "verde", titulo: "Éxito", mensaje: resultado.mensaje }))
            setValores({ fecha: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'), detalle: "", idUsuario: window.localStorage.getItem("idusuario") });
        } catch (error) {
            dispatch(setAlerta({ mostrar: true, tipo: "rosa", titulo: "Error", mensaje: error.message }))
        }
    }

    return (
        <Form className="d-flex flex-column" onSubmit={handleSubmit}>
            <FormGroup>
                <Form.Label>Categoría</Form.Label>
                <Form.Select aria-label="Default select example" name="idCategoria" onChange={handleChange} value={valoresActuales.idCategoria ?? 0}>
                    <option key={0} value={0} disabled>Seleccione una categoría</option>
                    {categorias?.map(c => <option key={c.id} value={c.id}>{c.tipo}</option>)}
                </Form.Select>
            </FormGroup>
            <FormGroup>
                <Form.Label>Fecha</Form.Label>
                <FormControl type="datetime-local" name="fecha" max={moment(new Date()).format('YYYY-MM-DDTHH:mm:ss')} onChange={handleChange} value={valoresActuales.fecha ?? moment(new Date()).format('YYYY-MM-DD HH:mm:ss')}></FormControl>
            </FormGroup>
            <Form.Group className="mb-3" controlId="LoginUsuario">
                <Form.Label>Detalle</Form.Label>
                <Form.Control type="text" placeholder="Ingrese detalle" name="detalle" onChange={handleChange} value={valoresActuales.detalle ?? ""} />
            </Form.Group>
            <Button variant="celeste" type="submit" >
                Agregar
            </Button>
        </Form>
    )
}

export default Agregar