import moment from 'moment';
import React, { useEffect, useState } from 'react'
import { Badge, Col, Row } from 'react-bootstrap';
import { useSelector } from 'react-redux'

const CountdownBiberon = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos)
    const biberones = eventos?.filter(e => e.idCategoria === 35);
    const [diferencia, setDiferencia] = useState(moment.duration(Date.now() - Date.now()));
    const [color, setColor] = useState("secondary")

    useEffect(() => {
        const interval = setInterval(() => {
            setDiferencia(calcDiferencia());
            setColor((calcDiferencia() >= 0) ? "verde" : "rosaO");
        }, 1000);

        return () => clearInterval(interval);
    }, [eventos]);

    const calcDiferencia = () => {
        if (biberones.length === 0) return moment.duration();

        const ultimoBiberon = biberones.reduce((max, actual) => {
            const fechaMax = new Date(max.fecha);
            const fechaActual = new Date(actual.fecha);
            return fechaActual > fechaMax ? actual : max;
        }, biberones[0]);
        const porxBiberon = moment(ultimoBiberon.fecha).add(4, 'h')

        return moment.duration(new Date(porxBiberon) - Date.now());
    };

    return (
        <Row className="d-felx text-center">
            <Col xs="auto" className="mx-1 px-0">
                <p className="mb-0">DÍAS</p>
                <h4><Badge bg={color} style={{ minWidth: '3rem' }}>{String(diferencia.days()).padStart(2, '0')}</Badge></h4>
            </Col>
            <Col className=" d-flex align-items-end mx-0 px-0">
                <h4>:</h4>
            </Col>
            <Col xs="auto" className="mx-1 px-0">
                <p className="mb-0">HH</p>
                <h4><Badge bg={color} style={{ minWidth: '3rem' }}>{String(diferencia.hours()).padStart(2, '0')}</Badge></h4>
            </Col>
            <Col className=" d-flex align-items-end mx-0 px-0">
                <h4>:</h4>
            </Col>
            <Col xs="auto" className="mx-1 px-0">
                <p className="mb-0">MM</p>
                <h4><Badge bg={color} style={{ minWidth: '3rem' }}>{String(diferencia.minutes()).padStart(2, '0')}</Badge></h4>
            </Col>
            <Col className=" d-flex align-items-end mx-0 px-0">
                <h4>:</h4>
            </Col>
            <Col xs="auto" className="mx-1 px-0">
                <p className="mb-0">SS</p>
                <h4><Badge bg={color} style={{ minWidth: '3rem' }}>{String(diferencia.seconds()).padStart(2, '0')}</Badge></h4>
            </Col>
        </Row>
    )
}

export default CountdownBiberon