import moment from 'moment';
import React, { useEffect, useState } from 'react'
import Chart from 'react-apexcharts';
import { useSelector } from 'react-redux';

const GraficaComidas = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos);
    const comidas = eventos?.filter(e => e.idCategoria === 31);


    const [graficoData, setGraficoData] = useState({
        series: [{
            name: 'Comidas',
            data: []
        }],
        options: {
            chart: {
                type: 'bar',
                height: 250
            },
            xaxis: {
                categories: []
            },
            colors: ['#ffb12b'],
        }
    });

    useEffect(() => {
        const hoy = moment();
        const semanaAnterior = Array.from({ length: 7 }, (_, i) => hoy.clone().subtract(i, 'days')).reverse();

        const labels = semanaAnterior.map(fecha => fecha.format('YYYY-MM-DD'));
        const data = semanaAnterior.map(fecha =>
            comidas.filter(comida => moment(comida.fecha).isSame(fecha, 'day')).length
        );

        setGraficoData({
            series: [{ name: 'Comidas', data }],
            options: {
                ...graficoData.options,
                xaxis: { categories: labels }
            }
        });
    }, [eventos]);


    return (
        <Chart
            options={graficoData.options}
            series={graficoData.series}
            type="bar"
            height={200}
        />
    )
}

export default GraficaComidas