import React, { useEffect } from 'react'
import { Card, Container } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { obtenerDepartamentosAPI } from '../services'
import { cargarDepartamentos } from '../redux/features/eventosSlice'
import RegistroFrom from './RegistroFrom'
import { useSelector } from 'react-redux';
import SpinnerCargando from './SpinnerCargando'

const Registro = () => {
    const cargando = useSelector((state) => state.cargandoSlice.cargando);
    const dispatch = useDispatch();
    useEffect(() => {
        callDepartamentos();
    }, [])

    const callDepartamentos = async () => {
        const departamentos = await obtenerDepartamentosAPI();
        dispatch(cargarDepartamentos(departamentos.departamentos))
    }

    return (
        <>
            <SpinnerCargando cargando={cargando}></SpinnerCargando>
            <Container fluid className={`content ${cargando ? 'disabled' : ''}`}>
                <Card style={{ width: '40%' }} className="mx-auto my-5 p-3">
                    <Card.Title>Crea una cuenta</Card.Title>
                    <RegistroFrom></RegistroFrom>
                    <Link to="/login">Volever</Link>
                </Card>
            </Container>
        </>
    )
}

export default Registro