import React from 'react'
import moment from 'moment';
import { Badge, Card, CardBody } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import TimerPanales from './TimerPanales';

const Panales = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos)
    const cantidad = (eventos?.filter(e => e.idCategoria === 33 && e.fecha >= moment(new Date()).format('YYYY-MM-DD')))?.length;
    return (
        <Card className="px-4" bg="verde" border="verde">
            <CardBody className="m-auto">
                <Card.Title className="text-center">Pañales</Card.Title>
                <h2 className="text-center"><Badge bg="verdeO">{cantidad}</Badge></h2>
                <TimerPanales />
            </CardBody>
        </Card>
    )
}

export default Panales