import React, { useEffect } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { obtenerCategoriasAPI, obtenerEventosAPI } from '../services'
import { agregarIniciales, cargarCategorias, } from '../redux/features/eventosSlice'
import { setCargando } from '../redux/features/cargandoSlice'
import EventosHoy from './EventosHoy'
import Agregar from './Agregar'
import NavbarMenu from './NavbarMenu'
import EventosAnteriores from './EventosAnteriores'
import Biberones from './Biberones'
import Panales from './Panales'
import Seccion from './Seccion'
import CountdownBiberon from './CountdownBiberon'
import GraficaCantidad from './GraficaCantidad'
import GraficaComidas from './GraficaComidas'
import SpinnerCargando from './SpinnerCargando'

const Dashboard = () => {
    const cargando = useSelector((state) => state.cargandoSlice.cargando);
    const dispatch = useDispatch();
    const navigate = useNavigate();

    useEffect(() => {
        if (apikey) {
            inicializacion();
        } else {
            navigate('/login')
        }

    }, [])

    const callEventos = async () => {
        const eventos = await obtenerEventosAPI();
        dispatch(agregarIniciales(eventos.eventos))
    }

    const callCategorias = async () => {
        const categorias = await obtenerCategoriasAPI();
        dispatch(cargarCategorias(categorias.categorias))
    }

    const inicializacion = async () => {
        dispatch(setCargando(true));
        await callEventos();
        await callCategorias();
        dispatch(setCargando(false));
    }

    let localStorage = window.localStorage;
    const apikey = localStorage.getItem("apikey");

    if (!apikey) {
        return (
            <SpinnerCargando cargando={cargando}></SpinnerCargando>
        )
    } else {
        return (
            <>
                <SpinnerCargando cargando={cargando}></SpinnerCargando>
                <div className={`content ${cargando ? 'disabled' : ''}`}>
                    <NavbarMenu></NavbarMenu>
                    <Container>
                        <Row className="my-3">
                            <Col xs={8}>
                                <Seccion componente={<EventosHoy />} titulo="Eventos recientes" clase="eventosHoy" />
                            </Col>
                            <Col xs={4}>
                                <Seccion componente={<Agregar />} titulo="Agregar nuevo evento" clase="eventosHoy" />
                            </Col>
                        </Row>
                        <Row className="my-3">
                            <Col xs={3}>
                                <Biberones></Biberones>
                            </Col>
                            <Col xs={3}>
                                <Panales></Panales>
                            </Col>
                            <Col xs={6}><Seccion componente={<CountdownBiberon />} titulo="Próximo biberón" clase="mx-auto" /></Col>
                        </Row>
                        <Row className="my-3">
                            <Col xs={7}>
                                <Seccion componente={<EventosAnteriores />} titulo="Eventos anteriores" clase="eventosAnteriores" />
                            </Col>
                            <Col xs={5}>
                                <Row className="mb-3">
                                    <Col> <Seccion componente={<GraficaCantidad />} titulo="Cantidades por categorías" clase="m-0 p-0"></Seccion> </Col>
                                </Row>
                                <Row>
                                    <Col><Seccion componente={<GraficaComidas />} titulo="Comidas recientes" clase="m-0 p-0" /></Col>
                                </Row>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </>

        )
    }
}

export default Dashboard