import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { login } from '../services';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { setCargando } from '../redux/features/cargandoSlice';
import { setAlerta } from '../redux/features/alertaSlice'

const LoginForm = () => {
    const [valoresActuales, setValores] = useState({});
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const handleChange = (e) => {
        const { name, value } = e.target;

        setValores((valoresActuales) => {
            const nuevosValores = { ...valoresActuales };

            if (value === "") {
                delete nuevosValores[name];
            } else {
                nuevosValores[name] = value;
            }
            return nuevosValores;
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        dispatch(setCargando(true));
        try {
            const resultado = await login(valoresActuales);
            dispatch(setAlerta({ mostrar: true, tipo: "verde", titulo: "Éxito", mensaje: "Usuario logueado con éxito" }))
            const localStorage = window.localStorage;
            localStorage.setItem("apikey", resultado.apiKey);
            localStorage.setItem("idusuario", resultado.id);
            localStorage.setItem("usuarioLogueado", valoresActuales.usuario);
            setValores({});
            navigate('/');
        } catch (error) {
            dispatch(setCargando(false))
            dispatch(setAlerta({ mostrar: true, tipo: "rosa", titulo: "Error", mensaje: error.message }))
        }

    }

    return (
        <Form onSubmit={handleSubmit} className="d-flex flex-column">
            <Form.Group className="mb-3" controlId="LoginUsuario">
                <Form.Label>Usuario</Form.Label>
                <Form.Control type="text" placeholder="Ingrese usuario" name="usuario" onChange={handleChange} value={valoresActuales.usuario ?? ""} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="LoginPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control type="password" placeholder="Password" name="password" onChange={handleChange} value={valoresActuales.password ?? ""} />
            </Form.Group>
            {(Object.keys(valoresActuales).length < 2) ?
                <Button variant="secundary" type="submit" disabled>
                    Ingresar
                </Button> :
                <Button variant="primary" type="submit">
                    Ingresar
                </Button>}
        </Form>
    )
}

export default LoginForm