export const agregarEventoAPI = async (evento) => {
    let apikey = localStorage.getItem("apikey");
    let iduser = localStorage.getItem("idusuario");
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json")
    myHeaders.append("apikey", apikey)
    myHeaders.append("iduser", iduser)

    var raw = JSON.stringify(evento);

    var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body: raw,
    }
    const retorno = await fetch(`${url}eventos.php`, requestOptions)
    .then(response => response.json())
    .then(json => {
        if (json.codigo === 200){
            return json;
        } else {
            return Promise.reject(json)
        }
    })
    .catch(error => {
        throw new Error(error.mensaje ? error.mensaje : "Hubo un error");
    })
    return retorno;
}

