import React from 'react'
import { Card } from 'react-bootstrap'

const Seccion = ({ componente, color, titulo, clase }) => {
    const cardProps = color ? { bg: color, border: color } : {};

    return (
        <Card {...cardProps} className="p-3">
            {titulo && <Card.Title>{titulo}</Card.Title>}
            <Card.Body className={clase ? `${clase}` : ""}>
                {componente}
            </Card.Body>
        </Card>
    )
}

export default Seccion