import React, { useEffect, useState } from 'react'
import moment from 'moment';
import { useSelector } from 'react-redux'
import { Badge, Card, } from 'react-bootstrap';

const TimerBiberones = () => {
    const eventos = useSelector((state) => state.eventosSlice.eventos);
    const biberones = eventos?.filter(e => e.idCategoria === 35);
    const [diferencia, setDiferencia] = useState(moment.duration(Date.now() - Date.now()));

    useEffect(() => {
        const interval = setInterval(() => {
            setDiferencia(calcDiferencia());
        }, 1000);

        return () => clearInterval(interval);
    }, [eventos]);

    const calcDiferencia = () => {
        if (biberones.length === 0) return moment.duration();

        const ultimoBiberon = biberones.reduce((max, actual) => {
            const fechaMax = new Date(max.fecha);
            const fechaActual = new Date(actual.fecha);
            return fechaActual > fechaMax ? actual : max;
        }, biberones[0]);

        return moment.duration(Date.now() - new Date(ultimoBiberon.fecha));
    };

    return (
        <div>
            <Card.Text className="text-center mb-0">
                Último
            </Card.Text>
            <Badge bg="secondary" style={{ minWidth: '2rem' }}>{String(diferencia.days()).padStart(2, '0')}</Badge>:
            <Badge bg="secondary" style={{ minWidth: '2rem' }}>{String(diferencia.hours()).padStart(2, '0')}</Badge>:
            <Badge bg="secondary" style={{ minWidth: '2rem' }}>{String(diferencia.minutes()).padStart(2, '0')}</Badge>:
            <Badge bg="secondary" style={{ minWidth: '2rem' }}>{String(diferencia.seconds()).padStart(2, '0')}</Badge>
        </div>
    );
}

export default TimerBiberones