import React, { useEffect, useState } from 'react'
import moment from 'moment';
import { Image } from 'react-bootstrap'
import { TrashFill } from 'react-bootstrap-icons';
import { useSelector, useDispatch } from 'react-redux'
import { eliminarEventoAPI } from '../services'
import { eliminarEvento } from '../redux/features/eventosSlice'
import { setAlerta } from '../redux/features/alertaSlice'

const Evento = ({ id, idCategoria, detalle, fecha }) => {
    const categorias = useSelector((state) => state.eventosSlice.categorias);
    const [categoria, setCategoria] = useState();
    const dispatch = useDispatch();

    useEffect(() => {
        const categoriaEncontrada = categorias.find(cat => cat.id === idCategoria);
        if (categoriaEncontrada) {
            setCategoria(categoriaEncontrada);
        }
    }, [idCategoria, categorias]);

    let tipo = "";
    let urlImg = "";
    if (categoria) {
        urlImg = `https://babytracker.develotion.com/imgs/${categoria.imagen}.png`;
        tipo = categoria.tipo;
    }

    const handleClick = async (e) => {
        try {
            const resultado = await eliminarEventoAPI(id);
            dispatch(eliminarEvento(id));
            console.log(resultado.mensaje)
            dispatch(setAlerta({ mostrar: true, tipo: "verde", titulo: "Éxito", mensaje: resultado.mensaje }))
        } catch (error) {
            dispatch(setAlerta({ mostrar: true, tipo: "rosa", titulo: "Error", mensaje: error.message }))
        }
    }

    return (
        <>
            <tr>
                <td><Image src={urlImg} roundedCircle /></td>
                <td><strong>{tipo}</strong></td>
                <td>{detalle}</td>
                {(moment(fecha).isSameOrAfter(moment().format('YYYY-MM-DD'))) ?
                    <td><strong>{moment(fecha).format('DD-MM-YYYY')}</strong> {moment(fecha).format('HH:mm')}</td> :
                    <td>{moment(fecha).format('DD-MM-YYYY')}</td>}
                <td><TrashFill className="trash-icon" size={30} onClick={handleClick} /></td>
            </tr>
        </>
    )
}

export default Evento